mr = (function (mr, $, window, document) {
    "use strict";
    
    var documentReady = function($) {

        var tag = document.createElement('script');
            tag.src = "https://www.youtube.com/iframe_api";

        var firstScriptTag = document.getElementsByTagName('script')[0];
            firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

        var loopVideo = document.getElementById("loop"); /** 5sec video loop */     
        var mobile = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
        var videoId = 'HW80bNk6eT0';
        var containerId = 'player';
        var done = false;
        var autoplay = 0;

        // var ua = navigator.userAgent.toLowerCase(); 
        // if (ua.indexOf('safari') != -1) { 
        //   if (ua.indexOf('chrome') > -1) {
        //     autoplay = 1;
        //   } else {
        //     autoplay = 0;
        //   }
        // }

        var player = {
            
            loadPlayer: function(container, videoId) {
                window.myPlayer = new YT.Player(container, {
                    playerVars: {
                        loop: 1,
                        playlist: videoId,
                        autoplay: autoplay,
                        rel: 0,
                        showinfo: 0,
                        controls: 0,
                        disablekb: 1,
                        modestbranding: 1,
                    },
                    height: 200,
                    width: 200,
                    videoId: videoId,
                    loop: 1,
                    events: {
                        'onReady': player.onPlayerReady,
                        'onStateChange': player.onPlayerStateChange
                    }
                });
            },

            playVideo: function(container, videoId) {
                if (typeof(YT) == 'undefined' || typeof(YT.Player) == 'undefined') {
                    window.onYouTubePlayerAPIReady = function() {
                        player.loadPlayer(container, videoId);
                    };
                    $.getScript('//www.youtube.com/player_api');
                } else {
                    player.loadPlayer(container, videoId);
                }
            },

            /** start video over with audio */
            playVideoFull: function() {
                loopVideo.pause();
                window.myPlayer.seekTo(0);
                window.myPlayer.playVideo();
            },
            
            /** loop video with no audio */
            stopVideo: function() {
                loopVideo.play();
                window.myPlayer.seekTo(0);
                window.myPlayer.stopVideo();
            },
            
            /** onload start video + muted */
            onPlayerReady: function(e) {
                // window.myPlayer.stop();
            },

            /** onload start video + muted */
            onPlayerStateChange: function(e) {
                if (e.data === 0) {
                    player.loopVideoAsBackground();
                }
            }
        }

        if( ! mobile ) {
            player.playVideo(containerId, videoId);
            $('.s-video-wrapper').removeClass('s-video-cover');
        }
        
        if( ! mobile ) {
            $(document).on('click', '.js-start-video', function(e) { 
                e.preventDefault();
                $('#scow-loop').hide();
                $('#scow-video').show();
                $('.s-cover-text').fadeOut();
                $('.s-features').fadeOut();
                $('.s-btn-stop').fadeIn();
                $('.s-video-wrapper iframe').css({opacity: 1});
                player.playVideoFull();
            });

            $(document).on('click', '.js-stop-video', function(e) { 
                e.preventDefault();
                $('#scow-loop').show();
                $('#scow-video').hide();
                $('.s-cover-text').fadeIn();
                $('.s-btn-stop').fadeOut();
                $('.s-features').fadeIn();
                $('.s-video-wrapper iframe').css({opacity: .6});
                player.stopVideo();
            });
        } else {
            $(document).on('click', '.s-btn-play', function(e) { 
                e.preventDefault();
                window.open("https://www.youtube.com/watch?v=HW80bNk6eT0", "_blank");
            });
        }

    };

    mr.video = {
        documentReady : documentReady        
    };

    mr.components.documentReady.push(documentReady);
    return mr;

}(mr, jQuery, window, document));