var mr = (function ($, window, document, undefined) {
    var mr = {};
    var components = { documentReady: [], documentReadyDeferred: [], windowLoad: [], windowLoadDeferred: []};

    $(document).ready(documentReady);
    $(window).on("load", windowLoad);

    function documentReady(context) {
        context = typeof context === typeof undefined ? $ : context;
        components.documentReady.concat(components.documentReadyDeferred).forEach(function(component){
            component(context);
        });

        $('.owl-carousel').owlCarousel({
            items: 2
        });

        var href;
        var modal_active = false;

        $(document).on('click', '.js-modal', function(e) {
            e.preventDefault();
            if ( !modal_active ) {
                modal_active = true
                href = $(this).attr('href');
                if ( href ) {
                    $(href).show();
                    $('body').addClass('modal-active');
                }
            }
        });
        
        $(document).on('click', '.js-modal-close', function(e) {
            e.preventDefault();
            modal_active = false;
            $('.s-modal').hide();
            $('body').removeClass('modal-active');
        });

        var $contactForm = $('#contact-form');
        var $contactButton = $('input[type="submit"]');

        $contactForm.submit(function(e) {
            e.preventDefault(); 
            $('.alert').remove();
            $.ajax({
                url: '//formspree.io/rvdriesten@gmail.com',
                method: 'POST',
                data: $(this).serialize(),
                dataType: 'json',
                beforeSend: function() {
                    $contactButton.prop('disabled', true);
                    $contactForm.append('<div class="alert alert--loading">Sending message</div>');
                },
                success: function(data) {
                    $contactButton.prop('disabled', false);
                    $contactForm.find('.alert--loading').hide();
                    // $contactForm.append('<div class="alert alert--success">Message sent!</div>');
                    window.location.href = "/thanks.html";
                    $('input[required]').value('');
                },
                error: function(err) {
                    $contactButton.prop('disabled', false);
                    $contactForm.find('.alert--loading').hide();
                    $contactForm.append('<div class="alert alert--error">Ops, there was an error.</div>');
                }
            });
        });

        /*
        $('.demo').simpleInstagramFancybox({
            // mode : 'user',
            //userID: '2155648748',
            userID: '42790596',
            accessToken : '42790596.9fd2b2a.e1486ea65b9e41839bcabcea2fbe9edd',
            tags : "cats",
            mode : 'popular'
        });
        */
    }

    function windowLoad(context) {
        context = typeof context === "object" ? $ : context;
        components.windowLoad.concat(components.windowLoadDeferred).forEach(function(component){
            component(context);
        });
    }

    mr.setContext = function (contextSelector) {
        var context = $;
        if(typeof contextSelector !== typeof undefined) {
            return function(selector){
                return $(contextSelector).find(selector);
            };
        }
        return context;
    };

    mr.components    = components;
    mr.documentReady = documentReady;
    mr.windowLoad    = windowLoad;
  
  return mr;
})(jQuery, window, document);
